@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@mixin f {
  font-family: 'Open Sans', sans-serif; }

$text: #1A1A3F;
$gray: #BABAC5;
$purple: #B452B5;
$gold: #CBB2A1;

body {
  @include f;
  font-size: 14px;
  color: $text;
  line-height: 1.3;
  &.no-scroll {
    overflow: hidden; } }
button,
input,
textarea,
select {
  @include f;
  font-size: 14px; }
button {
  background: none; }
a {
  text-decoration: none; }

.layout {
  display: flex;
  min-height: 100vh;
  background: #F5F5F7;
  &__sidebar {
    min-height: 100vh;
    padding: 35px 20px 35px 25px;
    background: white;
    flex: 0 0 196px; }
  &__container {
    min-height: 100vh;
    padding: 30px;
    flex: 0 0 calc(100% - 196px); } }

.btn {
  display: inline-block;
  padding: 8px 15px;
  background: white;
  border: 1px solid #D4D4DD;
  border-radius: 4px;
  font-size: 0;
  font-weight: 600;
  color: $text;
  text-align: center;
  vertical-align: middle;
  transition: all .2s;
  &:hover {
    background: #D4D4DD;
    .icon {
      fill: black; } }
  &__title,
  .icon {
    display: inline-block;
    vertical-align: middle; }
  &__title {
    font-size: 14px;
    font-weight: 600;
    line-height: 1.3; }
  .icon {
    margin-right: 10px;
    font-size: 12px;
    fill: $gray;
    transition: fill .2s; }
  &_gold {
    border-color: $gold;
    &:hover {
      background: darken($gold,10);
      border-color: darken($gold,10);
      color: white;
      .icon {
        fill: white; } } }
  &_gray {
    background: #F6F6F6;
    border-color: #F6F6F6;
    color: #00B24D;
    &:hover {
      border-color: #D4D4DD; } }
  &_def {
    border-radius: 0;
    border-color: $gold;
    text-transform: uppercase;
    &:hover {
      background: darken($gold,10);
      border-color: darken($gold,10);
      color: white; } }
  &_bg {
    border-radius: 0;
    border-color: $gold;
    background: $gold;
    color: white;
    text-transform: uppercase;
    &:hover {
      background: darken($gold,10);
      border-color: darken($gold,10); } } }

.panel {
  padding: 40px;
  background: white;
  box-shadow: 0 7px 12px 8px rgba(0,0,0,0.03);
  &__head {
    display: flex;
    margin-bottom: 30px;
    align-items: center; }
  &__title {
    font-size: 24px;
    font-weight: 700; }
  &__btn {
    margin-left: 25px; }
  &_md {
    max-width: 552px; } }

.logo {
  display: flex;
  margin-bottom: 34px;
  align-items: center;
  &__number {
    height: 37px;
    margin-right: 7px;
    background: #574883;
    border-radius: 2px;
    color: white;
    text-align: center;
    line-height: 40px;
    flex: 0 0 37px; }
  &__title {
    font-size: 16px;
    font-weight: 600;
    color: $text;
    text-transform: uppercase;
    letter-spacing: -0.26px; } }

.nav {
  &__title {
    margin-bottom: 24px;
    font-size: 12px;
    font-weight: 700;
    color: $gray;
    text-transform: uppercase; }
  &__list {
    margin-bottom: 34px; }
  &__link {
    display: flex;
    min-height: 22px;
    margin-bottom: 14px;
    color: $text;
    align-items: center; }
  &__link.active &__icon {
    .icon {
      fill: $purple; } }
  &__link.active &__label {
    color: $purple; }
  &__icon {
    margin-right: 15px;
    font-size: 0;
    flex: 0 0 16px;
    .icon {
      font-size: 16px;
      fill: $gray;
      &-password {
        position: relative;
        top: -2px;
        font-size: 21px; } } }
  &__label {
    font-size: 13px;
    font-weight: 700; } }

.preview {
  max-width: 104px;
  &__pic {
    display: block;
    max-width: 100%; } }

.field {
  &__label {
    margin-bottom: 13px;
    color: $gray;
    font-weight: 700; }
  &__input {
    width: 100%;
    height: 36px;
    padding: 0 12px;
    border-radius: 2px;
    text-align: center;
    &_bg {
      background: #F6F6F6; } }
  &__area {
    display: block;
    width: 100%;
    height: 88px;
    padding: 7px 12px;
    border: 1px solid #CECECE;
    border-radius: 2px;
    resize: vertical; }
  &__switch {
    margin-top: 7px; }
  &_reset &__switch {
    margin: 0; } }

.file {
  &__field {
    position: relative;
    height: 36px;
    margin-bottom: 17px;
    padding: 0 12px;
    border: 1px solid #CECECE;
    border-radius: 2px;
    overflow: hidden; }
  &__label {
    display: block;
    height: 36px;
    font-size: 12px;
    font-style: italic;
    color: #9898A5;
    line-height: 36px;
    @include text-overflow; }
  &__input {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    font-size: 32px;
    opacity: 0;
    cursor: pointer; }
  &__btn {
    min-width: 148px; } }

.actions {
  display: flex;
  &__btn {
    @include nl {
      margin-right: 10px; } } }

.table {
  &__row {
    display: flex;
    padding: 3px;
    border: 1px solid transparent;
    border-radius: 8px;
    align-items: center;
    transition: background .2s;
    &:hover {
      background: rgba($gray,.05); } }
  &__cell {
    padding: 9px;
    &:nth-child(1) {
      flex: 0 0 122px; }
    &:nth-child(2) {
      flex: 0 0 74px; }
    &:nth-child(3) {
      flex: 1; }
    &:nth-child(4) {
      flex: 0 0 268px; } }
  &__head {
    font-size: 14px;
    font-weight: 700;
    color: #BABAC5; }
  &__body &__row {
    margin-bottom: 12px;
    border-color: #E4E4E4; }
  &__title {
    padding: 9px 0; } }

.switch {
  display: inline-block;
  position: relative;
  vertical-align: top;
  user-select: none;
  cursor: pointer;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__input:checked + &__content &__preview {
    .icon {
      opacity: 1; } }
  &__content {
    display: block; }
  &__preview {
    display: block;
    width: 25px;
    height: 25px;
    border: 1px solid $gray;
    border-radius: 2px;
    .icon {
      display: block;
      margin: 3px 0 0 3px;
      font-size: 16px;
      fill: #00B24D;
      opacity: 0;
      transition: opacity .2s; } } }

.counter {
  display: flex;
  margin: 0 -11px;
  align-items: center;
  &__btn {
    padding: 11px;
    font-size: 0;
    &:hover {
      .icon {
        fill: black; } }
    .icon {
      font-size: 18px;
      transition: fill .2s; } }
  &__field {
    flex: 0 0 56px; } }

.photo {
  display: flex;
  align-items: center;
  &__preview {
    margin-right: 22px; }
  &__title {
    font-size: 12px;
    color: #9898A5;
    font-style: italic; } }

.form {
  &__photo {
    margin-bottom: 13px; }
  &__field {
    max-width: 395px;
    margin-bottom: 27px; }
  &__row {
    display: flex;
    margin: 0 -7px; }
  &__col {
    padding: 0 7px;
    flex: 0 0 162px; }
  &__foot {
    margin: 30px 0; }
  &__btn {
    width: 100%; } }





